/* Here goes the Global styles of the website*/

*,
*::after,
*::before {
  padding:0;
  margin:0;
  box-sizing : inherit;
}

html {
  box-sizing : border-box;
  font-size: 62.5%; /* 10px/16px = 62.5% --> 1rem = 10px*/
  font-weight :100;
}

body {
  font-size:sans-serif;
  line-height : 1.6182;
}

a {
    text-decoration:none ;
    color:inherit;
}

::placeholder {
  color:#949494f8;
}
